
import XHeader from 'vux/src/components/x-header/index.vue'

const url = require('../../assets/images/hcc_bg.png')

export default {
data () {
return {
status: 1, // 0 未开始 1 进行中或已结束
list: [],
bgImg: {
backgroundImage: 'url(' + url + ')'
}
}
},
methods: {
_getRanking () {
this.$tupHttp(this, 'manage-app/hccActivity/getRanking').then(res => {
if (res.data.extra.startTime) {
this.status = res.data.extra.startTime > new Date().valueOf() ? 0 : 1
}
this.list = res.data.models
this.$nextTick(() => {
let h = this.$refs.hccBox.getBoundingClientRect().bottom + 30
this.$refs.hccImage.style.height = h + 'px'
})
})
}
},
mounted () {
this._getRanking()
},
components: {
XHeader
}
}



